.services2 {
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    background-color: #ebebeb;
    background-color: #FFCBDB;
    padding-top: 30px;
    padding-bottom: 30px;
}

.services2 a {
    display: flex;
    box-sizing: border-box;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 30px;
}

#config {
    position: relative;
    left: -15px;
    width: 33px;
    height: 33px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}

.cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.cardContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    background-color: #ebebeb;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 90vw;
    max-width: 400px;
    height: 300px;
    border-radius: 15px;
    margin: 30px 30px;
    box-shadow: 0 2px 2px -2px rgb(59, 59, 59);
    -moz-box-shadow: 0 2px 2px -2px rgb(59, 59, 59);
    -webkit-box-shadow: 0 2px 4px -2px rgb(59, 59, 59);

}

.card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90vw;
    max-width: 400px;
    overflow: hidden;
    height: 200px;
    border-radius: 15px 15px 0 0;
    box-sizing: border-box;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
}

.card h3 {
    color: white;
    opacity: 1;
    margin: 0;
}

.cardContainer  span {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-color: white;
    box-sizing: border-box;
    width: 100%;
    padding: 10px 20px;
    height: 40%;
    font-weight: 600;
    color: rgba(55, 55, 55, 0.7);
}

.cardPng {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: rgba(55, 55, 55, 0.3);
    width: 100%;
    height: 100%;
}