.footer {
    display: flex;
    align-items: center;
    justify-content: space-around;
    bottom: 0px;
    color: black;
    width: 100%;
    overflow: hidden;
    padding-top: 30px;

}

.footer a, .services2 a {
    display: flex;
    box-sizing: border-box;
    width: fit-content;
    border-radius: 30px;
    width: 170px;
    align-items: center;
    justify-content: space-around;
}
.contactFooter div {
    background: #00C55E;
    background-color: #ff6f9c;
    border-radius: 30px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.contactFooter {
    display: flex;
    width: 100%;
    background: #0E3C7B;
    background-color: #5c5c5c ;
    height: 40px;
    justify-content: center;
    align-items: center;
    border-radius: 30px; 
    font-weight: 600;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

}



.contactFooter:active {
    background-color: #4d84d1;
    background-color: rgb(43, 43, 43);
}
.contactFooterContainer {
    display: flex;
    width: 40%;
}
.footerPart2 {
    opacity: 0.5;
    padding: 0 30px 0 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
}

.footerPart3 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 40%;
    padding: 0 30px 0 30px;
    box-sizing: border-box;
}

.footerPart3 {
    margin-bottom: 30px;
}

.footerPart3 a {
    background-color: #00C55E;
    background-color: #5c5c5c ;
    width: 44px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    border-radius: 0px;
}

.buttonsContatoFooter {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin-top: 30px;
}

.buttonsContatoFooter div {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    cursor: pointer;
    line-height: 1.8;
    font-weight: 500;
    font-size: 18px;
}

.buttonsContatoFooter div h3 {
    margin: 0;
    color: #0E3C7B;
    color: rgb(43, 43, 43);
    text-align: left;
}

.reservas {
    background-color: #0E3C7B;
    background-color: rgb(43, 43, 43);
    width: 100%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    font-size: 14px;
    font-weight: 500;
}

.reservas h3 {
    display: flex;
    font-size: 15px;
  
}

.reservas a {
    text-decoration: none;
    color: white;
}

.linhaVerticalFooter {
    height: 20px;
    width: 2px;
    background-color: white;
    margin: 0 10px;
   
}
.contactFooterContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.linkVitor a {
    margin-left: 5px;
}

.linkVitor a:active, .linkVitor a:hover {
    color: #00C55E;
}

@media only screen and (max-width: 1000px) {
    .footer {
        flex-direction: column;
    }
    .footerPart3 {
        width: 100%;
    }
    .reservas {
        flex-direction: column;
        justify-content: center;
    }

    .contactFooterContainer {
        width: 100%;
    }
    .linhaVerticalFooter {
        display: none;
    }
}