.services {
    color: black;
    display: flex;
    flex-wrap: wrap-reverse;
    box-sizing: border-box;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    overflow: hidden;
    padding-left: 5%;
    padding-right: 5%;
    background-color: #ebebeb;
    background-color: #FFCBDB;
    margin-top: 30px;
    margin-bottom: 30px;
}
.services .parte2 img {
    max-height: 400px;
}
.services h3 {
    font-weight: 600;
    line-height: 1.5;
    color: rgb(59, 59, 59);
}
.services .parte1{
    min-width: 350px; 
    width: 45vw;
    box-sizing: border-box;
}
.services .parte2 {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
}
.services .parte2 img  {

    max-width: 90vw;
}
.services h2, .services h3 {
    opacity: 0.5;
    text-align: left;
    margin: 20px 5%;
}
.parte1tais {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.tails {
    display: flex;
    justify-content: space-around;
    font-weight: 700;
    color: white;
    align-items: center;
    padding: 0px 20px;
    width: 90%;
    background-color: #00C666;
    background-color: #5c5c5c;
    margin: 10px 0px;
    height: 80px;
    box-sizing: border-box;
    font-size: 18px;
    max-width: 300px;
}
.tails img {
    position: relative;
    width: 46px;
    height: 46px;
}
.tails span {
    width: 100%;
    justify-content: center;
}

@media only screen and (max-width: 1250px)  {
 
    .tails  {
        max-width: 100%;
    }
}

@media only screen and (max-width: 1200px) {
    .services {
        padding-left: 2.5%;
        padding-right: 2.5%;
    }
 
}
@media only screen and (max-width: 1100px) {
    .services .parte1 {
        width: 90vw;
    }
    .services .parte2 {
        margin-top: 50px;
        margin-bottom: 50px;
    }
   
}

