.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.Home {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-size:  auto;
    background-position: 50% 0;
    background-repeat: no-repeat;
}
.textContainer {
    display: flex;
    width: 100%;
    z-index: 1;
}
.textPrincipal {
    
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    color: #fff;
    font-size: 1.5rem;
    font-weight: 800;
    text-align: left;
    width: 40%;
    position: relative;
    left: 10%;
    box-sizing: border-box;

}
.textPrincipal h1 {
    font-size: 70px;
    margin: 0;
}
.textPrincipal h2 {
    border-left: solid 5px white ;
    padding-left: 5px;
} 

.Home::before{
    -webkit-filter: blur(15px);
    -moz-filter: blur(15px);
    -o-filter: blur(15px);
    -ms-filter: blur(15px);
    filter: blur(15px);
}
.buttonContact {
   
    background-color: rgb(59, 59, 59);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 50px;
    position: relative;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    text-decoration: none !important;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 1px;
    outline: none;
    border: solid 3px #eeb844 ;
    border: solid 3px #0E3C7B;
    border: solid 3px #5c5c5c;
    border-radius: 15px;
    font-weight: 800;
    box-shadow: 0px 3px 10px -5px rgb(6 9 34 / 15%);
    transition: all 0.1s ease-in-out 0s;
    cursor: pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
    margin-top: 30px;
}

.buttonContact:active {
    background-color: transparent;
    border: solid 3px #5c5c5c;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
}
@media only screen and (max-width: 1000px) {
    .textContainer {
        position: relative;
        top: 150px;
    }
    .textPrincipal h1   {
        font-size: 2rem ;
    }
} 
@media only screen and (max-width: 990px)  {
    .textPrincipal {
        width: 80%;
    }
    .textPrincipal h1 {
        font-size: 2rem;
    }
  
}
@media only screen and (max-width: 600px) {
    .buttonContact{
        font-size: 18px;
        height: 30px;
        
    }
    .textPrincipal {
        padding-top: 20px;
    }
    .textPrincipal h1, .textPrincipal h2  {
        font-size: 1.5rem;
    }
    
}  


.header {
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    width: 100%;
    height: 15vw;
    font-size: 1.2rem;
    overflow-x: hidden;
    font-weight: 600;
    line-height: 80px;
    align-items: center;
    z-index: 1;
}

.showStatus {
    background-color: black;
    color: white;
    z-index: 3;
}

.headerBody {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    overflow: hidden;
    height: 100px;
    width: 80%;
    background-color: black;
    overflow: hidden;
}

.logo img {
    max-width: 80%;
    max-height: 100%;
}

.menus {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    right: 5%;
    width: 60%;
   
}
.menus a  {
    text-decoration: none;
    color: white;
}

