    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    overflow: hidden;
    box-sizing: border-box;
    border-bottom: solid 5px transparent;
    cursor: pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;

}

.focus {
    border-bottom: solid 5px;
    border-color: #eeb844;
}

.menuPort {
    display: none;
    right: 5%;
    position: relative;
    transition: 0.5s;
    overflow: hidden;
    box-sizing: border-box;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
    border-radius: 10px 10px 0px 0px;


}

.menuPort i {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 50px;
